@import 'variables';
@import 'mixins';

.contact {
  color: $colorWhite;

  @media (min-width: 320px) {
    &__h2 {
      font-weight: 500;
      font-size: 32px;
      line-height: 32px;
      letter-spacing: -1.5px;
      text-align: center;
    }
    &__name {
      padding-left: 16px;
      width: 277.96px;
      height: 48px;
      border-radius: 40px;
      background-color: $colorForms;
      margin-top: 48px;
      margin-bottom: 16px;
      text-align: left;
      border: none;
      outline: none;
    }

    &__email {
      padding-left: 16px;
      width: 277.96px;
      height: 48px;
      border-radius: 40px;
      background-color: $colorForms;
      text-align: left;
      border: none;
      outline: none;
    }

    &__texteria {
      padding-left: 16px;
      padding-top: 14px;
      resize: none;
      width: 277.96px;
      height: 148px;
      border-radius: 16px;
      background-color: $colorForms;
      margin-top: 16px;
      margin-bottom: 32px;
      text-align: left;
      border: none;
      outline: none;
    }

    &__button {
      display: block;
      background-color: $colorWhite;
      color: $bgColor;
      width: 280px;
      height: 56px;
      border-radius: 40px;
      margin-bottom: 48px;
      text-decoration: none;
      font-weight: 700;
      font-size: 16px;
      line-height: 27px;
      text-align: center;
      transition: box-shadow 0.2s;
      cursor: pointer;
      border: none;

      &:hover {
        box-shadow: 0 0 10px rgba(0, 123, 255, 0.7);
      }
    }

    &__parametrs {
      display: block;
      color: $colorWhite;
      text-decoration: none;
      font-weight: 400;
      font-size: 14px;
      line-height: 19.6px;
      transition: transform 0.3s;
      white-space: nowrap;

      &:hover {
        transform: scale(1.05);
      }

      &-txt {
        display: block;
        color: $colorWhite;
        text-decoration: none;
        padding-bottom: 24px;
        font-weight: 500;
        font-size: 16px;
        line-height: 22.4px;
        margin-top: 8px;
        transition: transform 0.3s;
        white-space: nowrap;

        &:hover {
          transform: scale(1.05);
        }
      }
    }
  }

  @media (min-width: 576px) {
    &__h2 {
      font-weight: 500;
      font-size: 48px;
      line-height: 48px;
      letter-spacing: -1.5px;
      text-align: center;
    }

    &__name {
      width: 100%;
      height: 48px;
      border-radius: 40px;
      background-color: $colorForms;
      margin-top: 48px;
      margin-bottom: 16px;
      text-align: left;
      border: none;
      outline: none;
      font-size: 14px;
    }

    &__email {
      width: 100%;
      height: 48px;
      border-radius: 40px;
      background-color: $colorForms;
      text-align: center;
      text-align: left;
      border: none;
      outline: none;
    }

    &__texteria {
      resize: none;
      width: 100%;
      height: 148px;
      border-radius: 16px;
      background-color: $colorForms;
      text-align: left;
      border: none;
      outline: none;
    }

    &__button {
      display: block;
      margin: 0 auto;
      background-color: $colorForms;
      color: $bgColor;
      width: 100%;
      height: 56px;
      border-radius: 40px;
      margin-bottom: 120px;
      text-decoration: none;
      font-weight: 700;
      font-size: 16px;
      line-height: 27px;
      text-align: center;
      cursor: pointer;
      border: none;

      &:hover {
        box-shadow: 0 0 10px rgba(0, 123, 255, 0.7);
      }

      &:hover {
        box-shadow: 0 0 10px rgba(0, 123, 255, 0.7);
      }
    }

    &__position {
      @include grid;
    }

    &__div {
      grid-column: 4 / 6;
    }

    &__form {
      grid-column: span 3;
    }

    &__parametrs {
      display: block;
      color: $colorWhite;
      text-decoration: none;
      font-weight: 400;
      font-size: 14px;
      line-height: 19.6px;
      transition: transform 0.3s;
      white-space: nowrap;

      &:hover {
        transform: scale(1.05);
      }

      &:first-child {
        margin-top: 48px;
      }

      &-txt {
        display: block;
        color: $colorWhite;
        text-decoration: none;
        margin-bottom: 24px;
        font-weight: 500;
        font-size: 16px;
        line-height: 22.4px;
        margin-top: 8px;
        white-space: nowrap;

        transition: transform 0.3s;

        &:hover {
          transform: scale(1.05);
        }
      }
    }
  }




  @media (min-width: 1200px) {
    &__h2 {
      padding-top: 180px;
      color: $colorWhite;
      font-weight: 600;
      font-size: 48px;
      line-height: 48px;
      letter-spacing: -1.5px;
      text-align: center;
    }

    &__name {
      width: 100%;
      height: 48px;
      border-radius: 40px;
      background-color: $colorForms;
      margin-top: 48px;
      margin-bottom: 16px;
      text-align: left;
      border: none;
      outline: none;
      font-size: 14px;
    }

    &__email {
      width: 100%;
      height: 48px;
      border-radius: 40px;
      background-color: $colorForms;
      text-align: left;
      border: none;
      outline: none;
      font-size: 14px;
    }

    &__texteria {
      resize: none;
      width: 100%;
      height: 148px;
      border-radius: 16px;
      background-color: $colorForms;
      font-size: 14px;
      text-align: left;
      border: none;
      outline: none;
    }

    &__button {
      display: block;
      background-color: $colorWhite;
      color: $bgColor;
      width: 100%;
      height: 56px;
      border-radius: 40px;
      margin-bottom: 182px;
      text-decoration: none;
      font-weight: 700;
      font-size: 16px;
      line-height: 27px;
      text-align: center;
      cursor: pointer;
      border: none;

      &:hover {
        box-shadow: 0 0 10px rgba(0, 123, 255, 0.7);
      }
    }

    &__position {
      @include grid;
    }

    &__div {
      grid-column: 8 / 11;
    }

    &__form {
      grid-column: span 5;
    }

    &__parametrs {
      display: block;
      color: $colorWhite;
      text-decoration: none;
      font-weight: 400;
      font-size: 14px;
      line-height: 19.6px;
      cursor: pointer;
      transition: transform 0.3s;
      white-space: nowrap;

      &:hover {
        transform: scale(1.05);
      }

      &-txt {
        display: block;
        color: $colorWhite;
        text-decoration: none;
        font-weight: 500;
        font-size: 16px;
        line-height: 22.4px;
        white-space: nowrap;
        cursor: pointer;
        transition: transform 0.3s;

        &:hover {
          transform: scale(1.05);
        }
      }
    }
  }
}
